inyandc","target":"_blank"}},[_vm._v("http://blog.sina.com.cn/wangxinyandc")]),_vm._v(" 给自己定下目标一月2篇原创博客，最多的一次月更7篇，截止到现在博客已经更新了120篇，每一篇博客看似简单，实则不然，首先要想写什么，然后要准备素材，背景介绍，举例说明，案例演示，个人实战，总结等一系列下来差不多要花3-7天(工作之余)时间，这期间不知道经历了多少个晚上公司加班和周末在家写博的经历，回想起来最大的感触就是做好一件事贵在坚持，坚持能够锻炼一个人的品质，坚持也是暗暗和自己较劲，自己也很感激着一路走来的坚持，把坚持养成一种习惯，无论是写新浪博客还是搭建自己个人网站，初心还是希望能够在自己从事的行业里留下自己的足迹，如果自己写的东西能够给同行们带来帮助那是最好不过了~")]),_c('p',{staticClass:"txt"},[_vm._v("最后，职业生涯中的最重要的2位leader 雄哥(彩讯)和华哥(乐信)，给了很多职场宝贵的意见和方法论，在此表示感谢！")])])]),_c('div',{staticClass:"back",on:{"click":_vm.backIndex}},[_vm._v("返回")])],1)
